import React from 'react'
import $ from 'jquery'

const promiseFile=(url,token)=>{
    return new Promise((resolve, reject)=>{
        $.ajax({
            url:url,
            type:'POST',
            cache:false,            // false 
            processData:false,      //: true,. DOM  false
            contentType:false,
            beforeSend(request){    // XMLHttpRequest  HTTP 
                request.setRequestHeader('Authorization', 'Bearer ' + token);
            },
            success(res){
                resolve(res) /*  */
            },
            fail(res){
                reject(res) /*  */
            }


        })
    })
}
export default promiseFile


